.beetwen{
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.right{
  float: right
}
.menu__item{
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  color: #0084CA;
  display: flex;
  flex-direction: column;
  text-align: left;
}
.submenu__item{
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  display: flex;
  align-items: center;
  color: #1D252E;
  padding: 1px 4px;
}
.submenu__item:hover{
  background: #e1e1e1;
  border-radius: 6px;
}
